.popup__image_close {
  position: absolute;
  height: 40px;
  width: 40px;
  right: 5%;
  top: 50px;
  color: white;
  margin: 0;
  padding: 0;
  border: none;
  background-color: transparent;
  background-image: url("../../../images/close-icon.svg");
  background-size: cover;
  cursor: pointer;
}



